import React, { useState, useEffect } from "react";
import { Slider, InputNumber, Checkbox, Input } from "antd";
import Button from "../../shared/Button/index";
import s from "./Price.module.scss";


const PriceComponent = ({ activeOption, setActiveOption }) => {

  const [minPrice, setMinPrice] = useState(0);
  const [maxPrice, setMaxPrice] = useState(100000);
  const [sliderValue, setSliderValue] = useState([0, 100000]);
  const [isOpen, setIsOpen] = useState(false);

  const handleChange = (event) => {
    setMinPrice(event[0]);
    setMaxPrice(event[1]);
    setSliderValue([event[0], event[1]]);
  }

  useEffect(() => {
    const listener = () => {
      if (!activeOption) return;
      setActiveOption(null);
    }
    document.addEventListener("click", listener)
    return () => document.removeEventListener("click", listener);
  }, [setActiveOption])

  return (
    <div className={`search__options-list__item${activeOption === "price" ? "-active" : ""}`} onClick={(e) => {
      setActiveOption("price");
      e.stopPropagation();
    }}>
      <span>Цена</span>

      <div className={`search__options-list__window ${s.wrapper}`} style={{
        opacity: activeOption === "price" ? "1": "0",
        visibility: activeOption === "price" ? "visible" : "hidden",
      }} onClick={(e) => e.stopPropagation()}>
        <Slider range defaultValue={sliderValue} disabled={false} handleStyle={{
          width: 40,
          height: 40
        }} tooltipVisible={false} onChange={handleChange} max={100000} value={sliderValue} />
        <div className={s.input__wrapper}>
          <div className="">
            <div className="input__sub">от</div>
            <div className={s.input__rubble}>
              <Input size="large" controls={false} value={minPrice} style={{width: 126}} onChange={(e) => {
                if (Number.isInteger(+e.target.value)) {
                  setMinPrice(+e.target.value);
                  setSliderValue([Math.max(0, +e.target.value), maxPrice])
                }
              }}/>
            </div>
          </div>
          <div className="">
            <div className="input__sub">до</div>
            <div className={s.input__rubble}>
              <Input size="large" controls={false} value={maxPrice} style={{width: 126}} onChange={(e) => {
                if (Number.isInteger(+e.target.value)) {
                  setMaxPrice(+e.target.value);
                  console.log("CHANGE");
                  setSliderValue([minPrice, Math.min(+e.target.value, 100000)])
                }
              }} />
            </div>
          </div>
        </div>
        <div className="window__bottom">
          <div className="window__bottom-container" onClick={() => {
            setMinPrice(0); setMaxPrice(100000);
            setSliderValue([0, 100000]);
          }}>
            <div className="window__bottom-clean" />
            <span>Очистить</span>
          </div>
          <Button onClick={() => {
            setActiveOption(null)
          }}>
            Применить
          </Button>
        </div>
      </div>
    </div>
  )
};

export default PriceComponent;
